

<template>
  <div class="home">
    <van-sticky :offset-top="0.00000001">
      <div class="home-top">
        <van-image
          width="100%"
          fit="fill"
          :src="require('../../assets/image/home/top.png')"
          alt="图片加载失败"
        />
        <div class="title">
          <van-image
            round
            fit="cover"
            :src="
              userInfo.imgPath ||
              require('../../assets/image/home/default-touxiang.png')
            "
            @click="
              ImageView(
                userInfo.imgPath ||
                  require('../../assets/image/home/default-touxiang.png')
              )
            "
            alt="图片加载失败"
          />
          <div class="title-info">
            <div class="name" v-if="userInfo">
              {{ userInfo.name }}
            </div>
            <div class="address" v-if="userInfo && userInfo.mobile">
              <span>Tel:</span>
              <span class="text">
                {{ userInfo.mobile }}
              </span>
            </div>
            <!-- <div class="address">
              <span>地址:</span>
              <span class="text">
                {{
                  locationName.length > 15
                    ? `${locationName.substring(0, 15)}···`
                    : locationName
                }}
              </span>
            </div> -->
          </div>
        </div>
        <div class="top-tabbar">
          <!-- <div @click="handleShowCode('passphrase')"> -->
          <div>
            <van-icon class="iconfont" class-prefix="icon" name="tongxingma" />
            <span>通行码</span>
          </div>
          <div class="line" />
          <div @click="handleShowCode('consumerCode')">
            <van-icon class="iconfont" class-prefix="icon" name="xiaofeima" />
            <span>消费码</span>
          </div>
        </div>
      </div>
    </van-sticky>
    <div class="bannerBox">
      <van-swipe
        class="my-swipe"
        :autoplay="3000"
        :height="150"
        indicator-color="white"
        lazy-render
      >
        <van-swipe-item v-for="(img, i) in images" :key="i">
          <van-image
            width="100%"
            height="100%"
            fit="fill"
            :src="img"
            alt="图片加载失败"
          />
        </van-swipe-item>
      </van-swipe>
    </div>
    <div class="content">
      <div>
        <h5>便捷服务</h5>
      </div>
      <div class="grid">
        <van-grid :column-num="3" square>
          <van-grid-item
            v-for="item in menus"
            :key="item.title"
            @click="item.click"
          >
            <!-- v-action:[item.code] -->
            <van-icon
              :class="{ iconfont: !item.isVant, 'van-grid-item__icon': true }"
              :class-prefix="item.isVant ? 'van-icon' : 'icon'"
              :name="item.icon"
            />
            <span class="van-grid-item__text"> {{ item.title }}</span>
          </van-grid-item>
        </van-grid>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { ImagePreview } from 'vant'
export default {
  name: 'Home',
  data() {
    return {
      // locationName: '湖北电信智慧大院',
      images: [
        //首页轮播图
        require('../../assets/image/home/banner/0.png'),
        require('../../assets/image/home/banner/1.png'),
        require('../../assets/image/home/banner/2.png'),
        require('../../assets/image/home/banner/3.png')
      ],
      menus: [
        //菜单列表
        {
          title: '智慧食堂',
          icon: 'zhihuishitang',
          // code: 'cscp.app.wisdomCanteen',
          click: () => {
            this.$router.push({ name: 'wisdomCanteenHome' })
          }
        },
        {
          title: '智慧通行',
          icon: 'zhihuitongxing',
          // code: 'cscp.app.wisdomEntrance',
          click: () => {}
        },
        {
          title: '访客管理',
          icon: 'fangkeguanli',
          // code: 'cscp.app.visitor',
          click: () => {
            // process.env.NODE_ENV === 'development'
            //   ? this.$router.push({ name: 'visitorHome1' })
            //   : this.$router.push({ name: 'visitorHome' })
          }
        }
      ]
    }
  },
  methods: {
    ImageView(img) {
      ImagePreview([img])
    },
    handleShowCode(name) {
      this.$router.push({ name })
    }
  },
  computed: mapState({
    userInfo: (state) => state.user.userInfo
  })
}
</script>

<style lang="less" scoped>
.home {
  .home-top {
    height: 180.55px;
    background: #f7f8fa;
    padding-bottom: 10px;
    .title {
      position: absolute;
      top: 30px;
      left: 12px;
      right: 10px;
      z-index: 10;
      display: flex;
      justify-content: start;
      align-items: center;
      color: #ffffffc9;
      .van-image {
        width: 80px;
        height: 80px;
        margin-right: 10px;
      }
      .name {
        font-size: 17px;
        margin-bottom: 10px;
      }
      .address,
      .tel {
        font-size: 10px;
      }
    }
    .top-tabbar {
      position: absolute;
      top: 136px;
      left: 12px;
      right: 12px;
      border-radius: 30px;
      background: #ffffff;
      z-index: 1000;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      box-shadow: 4px 10px 10px -15px #000;
      height: 43.5px;
      div {
        display: flex;
        justify-content: space-around;
        align-items: center;
      }
      .line {
        border-left: 1px solid #e3e3e3;
        height: 50%;
      }
      span {
        margin-left: 5px;
        font-size: 15px;
      }
    }
  }
  .bannerBox {
    width: 100%;
    .my-swipe {
      color: #fff;
      margin-top: 1px;
      text-align: center;
    }
  }
  .content {
    padding: 0 12px 10px;
    h5 {
      font-size: 17px;
      margin: 10px 0;
    }
    .grid {
      .van-grid {
        .iconfont {
          font-size: 28px;
        }
      }
    }
  }
}
</style>

